* {
    box-sizing: border-box;
  }
  
  body {
    background: url("./assets/images/background-page.svg");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    margin: 0;
  }

  :root {
    --font-button: "Odibee Sans", cursive;
    --background-victory: rgba(108, 180, 108, 0.7);
    --background-defeated: rgba(137, 73, 73, 0.7);
    --background-tie: rgba(116, 124, 130, 0.7);
  }
